import React from 'react'
import { Link } from 'gatsby'
import { injectIntl, intlShape, FormattedMessage } from 'react-intl'

//import LocalizedLink from '../LocalizedLink'

import locales from '../../constants/locales'

import PremiumLogo from "../../images/premium-logo.png"
import MediaQuery from 'react-responsive';

const Header = ({ intl: { locale } }) => (
<div class="nav-container">
<div className="nav-outer-wrapper">

    <div className="nav-wrapper-inner">
        <img src={PremiumLogo}/>
    </div>
    <div className="nav-wrapper-inner">
    <a href="" class="icon">&#9776;</a>

    <div className="topnav" id="main-nav">
            <a href={locale === 'en' ? '/' : `/${locale}/`}  className="white-txt font-small"><FormattedMessage id="navHome" /></a>        
            <a href={locale === 'en' ? '/access-premium' : `/${locale}/access-premium`}    className="white-txt font-small"><FormattedMessage id="navHowTo" /></a>        
            <a href={locale === 'en' ? '/faqs' : `/${locale}/faqs`}   className="white-txt font-small" ><FormattedMessage id="navFaqs" /></a>        
            <div class="dropdown">
                <button class="dropbtn font-small"><FormattedMessage id="navLanguages" /> 
                    <i class="fa fa-caret-down"></i>
                </button>
                <div class="dropdown-content">
                     {Object.keys(locales).map(key => (
                      //    <Link
                      //    key={locales[key].locale}
                      //    to={locales[key].default ? '/' : `/${locales[key].path}`}
                      //    className=""
                      //    >
                      //    {locales[key].locale}
                      //  </Link>

                       <a href={locales[key].default ? '/' : `/${locales[key].path}`} className="white-txt font-small">{locales[key].locale}</a>
                       ))}
                </div>
            </div> 

        </div>
    </div>
  </div>
</div>
)

Header.propTypes = { intl: intlShape.isRequired }

export default injectIntl(Header)